<template>
  <div class="chat-pdf-layout">
    <aside class="sidebar">
      <div class="sidebar-header">
        <h2>PDFËÅäÂ§©</h2>
        <p class="subtitle">Êô∫ËÉΩÊñáÊ°£Âä©Êâã</p>
      </div>
      <div class="chat-list">
        <div
          v-for="chat in chatList"
          :key="chat.chatId"
          :class="['chat-item', chat.chatId === currentChatId ? 'active' : '']"
          @click="switchChat(chat.chatId)"
        >
                  <div class="chat-summary">
          {{ chat.summary || 'ÔºàÁ©∫‰ºöËØùÔºâ' }}
        </div>
                  <div class="chat-time">{{ formatTime(chat.createTime) }}</div>
        </div>
      </div>
      <button class="new-chat-btn" @click="newChat">+ Êñ∞Âª∫‰ºöËØù</button>
    </aside>

    <main class="chat-main">
      <div class="chat-header" :class="{ 'collapsed': isHeaderCollapsed }">
        <div class="header-content">
          <h1>PDFÊô∫ËÉΩÂä©Êâã</h1>
          <div class="features">
            <span class="feature-tag">ÊñáÊ°£Ëß£Êûê</span>
            <span class="feature-tag">ÂÜÖÂÆπÈóÆÁ≠î</span>
            <span class="feature-tag">Ë¶ÅÁÇπÊèêÂèñ</span>
            <span class="feature-tag">Êô∫ËÉΩÊÄªÁªì</span>
          </div>
        </div>
        <button class="collapse-btn" @click="toggleHeader" :title="isHeaderCollapsed ? 'Â±ïÂºÄÊ†áÈ¢ò' : 'Êî∂Ëµ∑Ê†áÈ¢ò'">
          {{ isHeaderCollapsed ? '‚ñº' : '‚ñ≤' }}
        </button>
      </div>
      
      <!-- ÂÜÖÂÆπÂå∫Âüü -->
      <div class="content-area" v-if="pdfUrl">
        <!-- PDFÊü•ÁúãÂå∫Âüü -->
        <div class="pdf-viewer-container">
          <iframe 
            :src="pdfUrl" 
            class="pdf-viewer"
            frameborder="0"
          ></iframe>
        </div>

        <!-- ËÅäÂ§©Á™óÂè£ -->
        <div class="chat-window">
          <!-- ËÅäÂ§©ÂéÜÂè≤Âå∫Âüü -->
          <div class="chat-history-area">
            <div class="messages">
              <div v-for="(msg, idx) in currentMessages" :key="idx" :class="['message', msg.role]">
                <div class="message-avatar">
                  <div v-if="msg.role === 'user'" class="user-avatar">üë§</div>
                  <div v-else class="ai-avatar">ü§ñ</div>
                </div>
                <div class="message-content">
                  <!-- Áî®Êà∑Ê∂àÊÅØ -->
                  <template v-if="msg.role === 'user'">
                    <div class="user-message">
                      <span class="text">{{ msg.text }}</span>
                    </div>
                  </template>
                  
                  <!-- AIÊ∂àÊÅØ -->
                  <template v-else>
                    <!-- Âä†ËΩΩÂä®Áîª -->
                    <div v-if="loading && idx === currentMessages.length - 1 && !msg.text" class="loading-dots">
                      <span class="dot"></span>
                      <span class="dot"></span>
                      <span class="dot"></span>
                    </div>
                    
                    <!-- ÊúÄÁªàÂõûÂ§ç -->
                    <div v-if="msg.text" class="ai-response">
                      <div class="response-text" v-html="formatResponse(msg.text)"></div>
                    </div>
                  </template>
                </div>
              </div>
            </div>
          </div>
          
          <!-- ÂçïË°åËæìÂÖ•Âå∫Âüü -->
          <div class="input-bar-container">
            <div class="input-wrapper">
              <textarea
                v-model="input"
                @input="autoResizeInput"
                @keydown="handleKeydown"
                placeholder="ËØ∑ËæìÂÖ•ÊÇ®ÁöÑÈóÆÈ¢ò..."
                :disabled="loading || !pdfUrl"
                class="chat-input textarea-auto"
                rows="1"
                style="resize:none;overflow:hidden;"
              />
              <div class="input-actions">
                <button 
                  @click="send" 
                  :disabled="loading || !pdfUrl || !input.trim()"
                  class="send-btn"
                >
                  <span v-if="loading">ÂèëÈÄÅ‰∏≠...</span>
                  <span v-else>ÂèëÈÄÅ</span>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>

      <!-- PDF‰∏ä‰º†Âå∫Âüü -->
      <div class="pdf-upload-area" v-if="!pdfUrl">
        <div class="upload-placeholder" @click="$refs.fileInput.click()">
          <p>ÁÇπÂáª‰∏ä‰º†PDFÊñá‰ª∂</p>
          <p class="upload-hint">ÊîØÊåÅPDFÊ†ºÂºèÊñá‰ª∂</p>
        </div>
        <input 
          type="file" 
          ref="fileInput" 
          @change="handleFileUpload" 
          accept=".pdf"
          style="display: none;"
        />
      </div>
    </main>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue'
import axios from 'axios'

function genId() {
  return 'chat_' + Date.now() + '_' + Math.random().toString(36).slice(2, 8)
}

export default {
  name: 'ChatPDF',
  setup() {
    const chatList = ref([])
    const currentChatId = ref('')
    const input = ref('')
    const loading = ref(false)
    const currentMessages = ref([])
    const pdfUrl = ref('')
    const currentPdfName = ref('')
    const isHeaderCollapsed = ref(false)
    
    // ‰∏∫ÊØè‰∏™‰ºöËØùÁª¥Êä§Áã¨Á´ãÁöÑÊ∂àÊÅØ
    const sessionMessages = ref({}) // { chatId: messages[] }
    const sessionLoading = ref({}) // { chatId: boolean }
    const sessionPdf = ref({}) // { chatId: { url, name } }

    // Ëé∑Âèñ‰ºöËØùÂàóË°®
    const fetchChatList = async () => {
      try {
        const resp = await axios.get('/ai/history/pdf/with-time')
        // {chatId: createTime} Êò†Â∞Ñ
        const chatIdsWithTime = resp.data
        const chatListWithTitles = []
        
        // ‰∏∫ÊØè‰∏™‰ºöËØùÊ£ÄÊü•PDFÊñá‰ª∂Âπ∂Ëé∑ÂèñÂêçÁß∞‰Ωú‰∏∫Ê†áÈ¢ò
        for (const [chatId, createTime] of Object.entries(chatIdsWithTime)) {
          try {
            // Ê£ÄÊü•PDFÊñá‰ª∂ÊòØÂê¶Â≠òÂú®
            const fileUrl = `${axios.defaults.baseURL}/ai/pdf/file/${chatId}`;
            const response = await fetch(fileUrl, { method: 'HEAD' });
            let title = 'ÔºàÁ©∫‰ºöËØùÔºâ'
            
            if (response.ok) {
              // Â¶ÇÊûúÊñá‰ª∂Â≠òÂú®Ôºå‰ΩøÁî®PDFÂêçÁß∞‰Ωú‰∏∫Ê†áÈ¢ò
              title = `PDF_${chatId.substring(0, 8)}.pdf`
            }
            
            chatListWithTitles.push({ 
              chatId, 
              summary: title,
              createTime: createTime
            })
          } catch (error) {
            // Â¶ÇÊûúÊ£ÄÊü•Â§±Ë¥•Ôºå‰ΩøÁî®ÈªòËÆ§Ê†áÈ¢ò
            chatListWithTitles.push({ 
              chatId, 
              summary: 'ÔºàÁ©∫‰ºöËØùÔºâ',
              createTime: createTime
            })
          }
        }
        
        // ÊåâÂàõÂª∫Êó∂Èó¥ÊéíÂ∫èÔºåÊúÄÊñ∞ÁöÑÂú®ÂâçÈù¢
        chatListWithTitles.sort((a, b) => b.createTime - a.createTime)
        
        chatList.value = chatListWithTitles
        // Ëá™Âä®ÈÄâ‰∏≠Á¨¨‰∏Ä‰∏™
        if (chatList.value.length > 0 && !currentChatId.value) {
          currentChatId.value = chatList.value[0].chatId
        }
      } catch (error) {
        console.error('Ëé∑Âèñ‰ºöËØùÂàóË°®Â§±Ë¥•:', error)
      }
    }

    // Ëé∑ÂèñÂΩìÂâç‰ºöËØùÊ∂àÊÅØ
    const fetchMessages = async (chatId) => {
      if (!chatId) return
      try {
        const resp = await axios.get(`/ai/history/pdf/${chatId}`)
        // resp.data: [{role, content}]
        // ÂÖºÂÆπÂéüÊúâÁªìÊûÑ
        const messages = resp.data.map(msg => {
          if (msg.role === 'assistant') {
            return { role: 'ai', text: msg.content }
          } else {
            return { role: 'user', text: msg.content }
          }
        })
        
        // Êõ¥Êñ∞‰ºöËØùÊ∂àÊÅØ
        sessionMessages.value[chatId] = messages
        // Â¶ÇÊûúÊòØÂΩìÂâç‰ºöËØùÔºåÊõ¥Êñ∞currentMessages
        if (chatId === currentChatId.value) {
          currentMessages.value = messages
          // ÊªöÂä®Âà∞Â∫ïÈÉ®
          scrollToBottom()
        }
        
        // Ê£ÄÊü•ËØ•‰ºöËØùÊòØÂê¶ÊúâPDFÊñá‰ª∂ÔºàÈÄöËøáÂêëÂêéÁ´ØÂèëÈÄÅHEADËØ∑Ê±ÇÊ£ÄÊü•Êñá‰ª∂ÊòØÂê¶Â≠òÂú®Ôºâ
        try {
          const fileUrl = `${axios.defaults.baseURL}/ai/pdf/file/${chatId}`;
          const response = await fetch(fileUrl, { method: 'HEAD' });
          if (response.ok) {
            // Â¶ÇÊûúÊñá‰ª∂Â≠òÂú®ÔºåËÆæÁΩÆPDF‰ø°ÊÅØ
            const pdfInfo = {
              url: fileUrl,
              name: `PDF_${chatId.substring(0, 8)}.pdf`
            };
            sessionPdf.value[chatId] = pdfInfo;
            if (chatId === currentChatId.value) {
              pdfUrl.value = pdfInfo.url;
              currentPdfName.value = pdfInfo.name;
            }
          }
        } catch (fileError) {
          // Êñá‰ª∂Ê£ÄÊü•Â§±Ë¥•Ôºå‰∏çÂΩ±ÂìçÊ∂àÊÅØÂä†ËΩΩ
          console.debug('Ê£ÄÊü•PDFÊñá‰ª∂ÊòØÂê¶Â≠òÂú®Êó∂Âá∫Èîô:', fileError);
        }
        
        // Â¶ÇÊûúËØ•‰ºöËØùÂ∑≤ÁªèÊúâPDFÊñá‰ª∂‰ø°ÊÅØÔºåÂä†ËΩΩPDF‰ø°ÊÅØ
        if (sessionPdf.value[chatId]) {
          pdfUrl.value = sessionPdf.value[chatId].url
          currentPdfName.value = sessionPdf.value[chatId].name
        }
      } catch (error) {
        console.error('Ëé∑ÂèñÊ∂àÊÅØÂ§±Ë¥•:', error)
      }
    }

    // Êñ∞Âª∫‰ºöËØù
    const newChat = async () => {
      const id = genId()
      currentChatId.value = id
      sessionMessages.value[id] = []
      currentMessages.value = []
      pdfUrl.value = ''
      currentPdfName.value = ''
      sessionPdf.value[id] = { url: '', name: '' }
      // ‰∏çÁ´ãÂç≥Ê∑ªÂä†Âà∞ËÅäÂ§©ÂàóË°®ÔºåÁ≠âÂèëÈÄÅÊ∂àÊÅØÊó∂ÂÜçÊ∑ªÂä†
    }

    // ÂàáÊç¢‰ºöËØù
    const switchChat = async (id) => {
      currentChatId.value = id
      // Â¶ÇÊûú‰ºöËØùÊ∂àÊÅØÂ∑≤Â≠òÂú®ÔºåÁõ¥Êé•‰ΩøÁî®
      if (sessionMessages.value[id]) {
        currentMessages.value = sessionMessages.value[id]
        // Ê£ÄÊü•ÂΩìÂâç‰ºöËØùÊòØÂê¶ÊúâPDF
        if (sessionPdf.value[id]) {
          pdfUrl.value = sessionPdf.value[id].url
          currentPdfName.value = sessionPdf.value[id].name
        } else {
          // Â¶ÇÊûúsession‰∏≠Ê≤°ÊúâPDF‰ø°ÊÅØÔºåÂ∞ùËØï‰ªéÂêéÁ´ØÊ£ÄÊü•Êñá‰ª∂ÊòØÂê¶Â≠òÂú®
          try {
            const fileUrl = `${axios.defaults.baseURL}/ai/pdf/file/${id}`;
            const response = await fetch(fileUrl, { method: 'HEAD' });
            if (response.ok) {
              // Â¶ÇÊûúÊñá‰ª∂Â≠òÂú®ÔºåËÆæÁΩÆPDF‰ø°ÊÅØ
              const pdfInfo = {
                url: fileUrl,
                name: `PDF_${id.substring(0, 8)}.pdf`
              };
              sessionPdf.value[id] = pdfInfo;
              pdfUrl.value = pdfInfo.url;
              currentPdfName.value = pdfInfo.name;
              
              // Êõ¥Êñ∞ËÅäÂ§©ÂàóË°®‰∏≠ÁöÑÊ†áÈ¢ò
              const chatItem = chatList.value.find(item => item.chatId === id)
              if (chatItem) {
                chatItem.summary = pdfInfo.name
              }
            }
          } catch (error) {
            console.debug('Ê£ÄÊü•PDFÊñá‰ª∂ÊòØÂê¶Â≠òÂú®Êó∂Âá∫Èîô:', error);
          }
        }
        // ÊªöÂä®Âà∞Â∫ïÈÉ®
        scrollToBottom()
      } else {
        // Âê¶Âàô‰ªéÂêéÁ´ØËé∑Âèñ
        await fetchMessages(id)
      }
    }

    // ÊªöÂä®Âà∞Â∫ïÈÉ®ÂáΩÊï∞
    const scrollToBottom = () => {
      setTimeout(() => {
        const messagesEl = document.querySelector('.messages')
        if (messagesEl) {
          messagesEl.scrollTop = messagesEl.scrollHeight
        }
      }, 10)
    }
    
    // Ê†ºÂºèÂåñÊó∂Èó¥
    const formatTime = (timestamp) => {
      if (timestamp) {
        const date = new Date(timestamp)
        return date.toLocaleDateString() + ' ' + date.toLocaleTimeString()
      }
      return ''
    }
    
    // Ê†ºÂºèÂåñÂìçÂ∫îÊñáÊú¨
    const formatResponse = (text) => {
      if (!text) return ''
      // ÁÆÄÂçïÁöÑÊç¢Ë°åÁ¨¶Â§ÑÁêÜ
      return text.replace(/\n/g, '<br>')
    }
    
    // ÂàáÊç¢Â§¥ÈÉ®Êî∂Áº©Áä∂ÊÄÅ
    const toggleHeader = () => {
      isHeaderCollapsed.value = !isHeaderCollapsed.value
    }

    // Â§ÑÁêÜÊñá‰ª∂‰∏ä‰º†
    const handleFileUpload = async (event) => {
      // Á°Æ‰øùÊúâÊúâÊïàÁöÑchatId
      if (!currentChatId.value) {
        await newChat();
      }
      
      const file = event.target.files[0]
      if (!file || file.type !== 'application/pdf') {
        alert('ËØ∑ÈÄâÊã©PDFÊñá‰ª∂')
        return
      }
      
      // Ê£ÄÊü•Êñá‰ª∂Â§ßÂ∞èÔºåÈôêÂà∂‰∏∫100MB
      const maxSize = 100 * 1024 * 1024; // 100MB in bytes
      if (file.size > maxSize) {
        alert('Êñá‰ª∂Â§ßÂ∞è‰∏çËÉΩË∂ÖËøá100MB')
        event.target.value = '' // Ê∏ÖÁ©∫Êñá‰ª∂ËæìÂÖ•Ê°Ü
        return
      }
      
      const formData = new FormData()
      formData.append('file', file)
      
      try {
        // ‰øÆÊîπ‰∏ä‰º†Êé•Âè£Ë∑ØÂæÑÔºå‰∏éÂêéÁ´ØPdfControllerÂåπÈÖç
        const response = await axios.post(`/ai/pdf/upload/${currentChatId.value}`, formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        })
        
        // ‰øÆÊîπÊ£ÄÊü•ÈÄªËæëÔºåÂåπÈÖçÂêéÁ´ØÂÆûÈôÖËøîÂõûÁöÑÊï∞ÊçÆÁªìÊûÑ
        if (response.data.ok !== 1) {
          throw new Error(response.data.msg || '‰∏ä‰º†Â§±Ë¥•')
        }
        
        // ‰øùÂ≠òPDF‰ø°ÊÅØÂà∞ÂΩìÂâç‰ºöËØù
        const chatId = currentChatId.value
        // ‰øÆÊîπÊñá‰ª∂‰∏ãËΩΩË∑ØÂæÑÔºå‰∏éÂêéÁ´ØPdfControllerÂåπÈÖç
        // ‰ΩøÁî®ÂÆåÊï¥ÁöÑURLÁ°Æ‰øùË∞ÉÁî®ÂêéÁ´ØÊúçÂä°ÁöÑÁ´ØÂè£(8080)
        const pdfInfo = {
          url: `${axios.defaults.baseURL}/ai/pdf/file/${chatId}`,
          name: file.name
        }
        
        sessionPdf.value[chatId] = pdfInfo
        pdfUrl.value = pdfInfo.url
        currentPdfName.value = pdfInfo.name
        
        // Êõ¥Êñ∞ËÅäÂ§©ÂàóË°®‰∏≠ÁöÑÊ†áÈ¢ò
        const chatItem = chatList.value.find(item => item.chatId === chatId)
        if (chatItem) {
          chatItem.summary = file.name
        }
        
        // Ê∏ÖÁ©∫Êñá‰ª∂ËæìÂÖ•Ê°Ü
        event.target.value = ''
      } catch (error) {
        console.error('‰∏ä‰º†PDFÂ§±Ë¥•:', error)
        alert('‰∏ä‰º†PDFÂ§±Ë¥•ÔºåËØ∑ÈáçËØï')
      }
    }

    // ÂÖ≥Èó≠PDFÊü•ÁúãÂô®
    const closePdf = () => {
      pdfUrl.value = ''
      currentPdfName.value = ''
      // ‰ªéÂΩìÂâç‰ºöËØù‰∏≠ÁßªÈô§PDF‰ø°ÊÅØ
      const chatId = currentChatId.value
      if (sessionPdf.value[chatId]) {
        sessionPdf.value[chatId] = { url: '', name: '' }
        // Êõ¥Êñ∞ËÅäÂ§©ÂàóË°®‰∏≠ÁöÑÊ†áÈ¢ò
        const chatItem = chatList.value.find(item => item.chatId === chatId)
        if (chatItem) {
          chatItem.summary = 'ÔºàÁ©∫‰ºöËØùÔºâ'
        }
      }
    }

    // ÂèëÈÄÅÊ∂àÊÅØ
    const send = async () => {
      if (!input.value.trim() || loading.value || !pdfUrl.value) return
      
      const chatId = currentChatId.value
      const prompt = input.value
      input.value = ''
      // ËÆæÁΩÆÂΩìÂâç‰ºöËØù‰∏∫Âä†ËΩΩÁä∂ÊÄÅ
      sessionLoading.value[chatId] = true
      loading.value = true
      
      // Ëé∑ÂèñÂΩìÂâç‰ºöËØùÁöÑÊ∂àÊÅØÂàóË°®
      let messages = sessionMessages.value[chatId] || []
      
      // Â¶ÇÊûúÊòØÁ¨¨‰∏ÄÊù°Ê∂àÊÅØÔºåÂÖàÊ∑ªÂä†Âà∞ËÅäÂ§©ÂàóË°®
      if (messages.length === 0) {
        const currentTime = Date.now()
        chatList.value.unshift({ 
          chatId: chatId, 
          summary: prompt.slice(0, 20) + (prompt.length > 20 ? '...' : ''),
          createTime: currentTime
        })
      } else {
        // Â¶ÇÊûú‰∏çÊòØÁ¨¨‰∏ÄÊù°Ê∂àÊÅØÔºåÊõ¥Êñ∞‰ºöËØùÊó∂Èó¥
        const currentChat = chatList.value.find(c => c.chatId === chatId)
        if (currentChat) {
          currentChat.createTime = Date.now()
        }
      }
      
      // ÂÖàÊ∑ªÂä†Áî®Êà∑Ê∂àÊÅØÂà∞ÁïåÈù¢
      messages.push({ role: 'user', text: prompt })
      sessionMessages.value[chatId] = messages
      
      // Á´ãÂç≥ÂàõÂª∫‰∏Ä‰∏™Á©∫ÁöÑAIÊ∂àÊÅØÔºåÊòæÁ§∫"AIÔºö"ÔºåÈÅøÂÖçÂç°È°øÊÑü
      messages.push({ role: 'ai', text: '' })
      sessionMessages.value[chatId] = messages
      
      // Â¶ÇÊûúÊòØÂΩìÂâç‰ºöËØùÔºåÊõ¥Êñ∞currentMessages
      if (chatId === currentChatId.value) {
        currentMessages.value = messages
        // ÊªöÂä®Âà∞Â∫ïÈÉ®
        scrollToBottom()
      }
      
      // ÂêéÂè∞Â§ÑÁêÜAIÂõûÂ§çÔºå‰º†ÈÄíchatIdÂèÇÊï∞
      processAIResponse(chatId, prompt, messages)
    }

    // ÂêéÂè∞Â§ÑÁêÜAIÂõûÂ§ç
    const processAIResponse = async (chatId, prompt, messages) => {
      try {
        // ÊûÑÂª∫ËØ∑Ê±ÇURLÔºà‰øùÁïôÂéüÊúâaxiosÂèÇÊï∞Â§ÑÁêÜÔºâ
        const url = axios.getUri({ 
          url: '/ai/pdf/chat', 
          params: { prompt, chatId }
        });
        
        // ‰ΩøÁî®fetchËé∑ÂèñÊµÅÂºèÂìçÂ∫î [6,8](@ref)
        const response = await fetch(url);
        if (!response.body) throw new Error('Êó†ÊµÅÂºèÂìçÂ∫î');
        
        const reader = response.body.getReader();
        const decoder = new TextDecoder('utf-8');
        let aiText = '';
        
        // ‰ΩøÁî®‰∏¥Êó∂Ê∂àÊÅØÁ¥¢Âºï
        const tempMsgIdx = messages.length - 1;
        
        // ÊµÅÂºèÂ§ÑÁêÜÔºà‰øÆÂ§çeslintÈîôËØØÔºâ[1](@ref)
        // eslint-disable-next-line no-constant-condition
        while (true) {
          const { done, value } = await reader.read();
          if (done) break;
          
          // Áõ¥Êé•Ëß£Á†ÅÂ≠óÁ¨¶‰∏≤ÁâáÊÆµÔºàFluxÁâπÊÄßÔºâ[3](@ref)
          const textChunk = decoder.decode(value, { stream: true });
          aiText += textChunk;
          
          // ÂÆûÊó∂Êõ¥Êñ∞Ê∂àÊÅØÊñáÊú¨
          messages[tempMsgIdx].text = aiText;
          
          // Êõ¥Êñ∞‰ºöËØùÊ∂àÊÅØ
          sessionMessages.value[chatId] = [...messages];
          
          // ÂΩìÂâç‰ºöËØùÂ§ÑÁêÜÔºàÂê´ÊªöÂä®Ôºâ
          if (chatId === currentChatId.value) {
            currentMessages.value = [...messages];
            scrollToBottom();
          }
        }
        
        // ÊúÄÁªàÊõ¥Êñ∞‰ºöËØùÂàóË°®‰ª•Ëé∑ÂèñÊúÄÊñ∞ÁöÑÊó∂Èó¥
        await fetchChatList();
      } catch (e) {
        const errorMsgIdx = messages.length - 1;
        messages[errorMsgIdx] = { 
          role: 'ai', 
          text: `[AIÂºÇÂ∏∏] ${e.message || 'Êú™Áü•ÈîôËØØ'}`
        };
        sessionMessages.value[chatId] = [...messages];
        if (chatId === currentChatId.value) {
          currentMessages.value = [...messages];
          scrollToBottom();
        }
      } finally {
        // Ê∏ÖÁêÜÂä†ËΩΩÁä∂ÊÄÅ
        sessionLoading.value[chatId] = false;
        if (chatId === currentChatId.value) {
          loading.value = false;
        }
      }
    }

    // Â§ÑÁêÜÈîÆÁõò‰∫ã‰ª∂
    const handleKeydown = (e) => {
      if (e.key === 'Enter') {
        if (e.shiftKey) {
          // Shift+Enter: Êç¢Ë°å
          return;
        } else {
          // Enter: ÂèëÈÄÅÊ∂àÊÅØ
          e.preventDefault();
          send();
        }
      }
    };

    // Ëá™Âä®Ë∞ÉÊï¥ËæìÂÖ•Ê°ÜÈ´òÂ∫¶
    const autoResizeInput = (e) => {
      const textarea = e.target;
      textarea.style.height = 'auto';
      textarea.style.height = textarea.scrollHeight + 'px';
    };

    // ÂàùÂßãÂåñÂä†ËΩΩ
    onMounted(async () => {
      await fetchChatList()
      if (currentChatId.value) {
        await fetchMessages(currentChatId.value)
      }
    })

    return {
      chatList,
      currentChatId,
      currentMessages,
      input,
      send,
      newChat,
      switchChat,
      loading,
      pdfUrl,
      currentPdfName,
      handleFileUpload,
      closePdf,
      autoResizeInput,
      formatTime,
      formatResponse,
      isHeaderCollapsed,
      toggleHeader,
      handleKeydown
    }
  }
}
</script>

